import React, { useState } from 'react';
import styled, { css } from 'styled-components';
import { useAuthDispatch } from '../../context/AuthContext';
import { EMAIL_EXP, PASSWORD_EXP } from '../../util/expression';
import { appendUserInGoogleSheet, findUserInGoogleSheet } from '../../util/google.sheets';
import SignupHeader from './SignupHeader';

const SignupForm = styled.form`
    background: #f8f9fa;

    width: 100%;
    max-width: 500px;

    margin: 0 auto;

    padding-top: 32px;
    padding-bottom: 72px;
    
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
    border-top: 1px solid #e9ecef;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const SignupLabel = styled.div`
    width: 100%;
    font-size: 16px;
    margin: 5px 0;

    ${props =>
        props.error &&
        css`
            color: red;
        `
    } 
`;

const SignupInput = styled.input`
    padding: 12px;
    margin: 5px 0;
    border-radius: 4px;
    border: 1px solid #dee2e6;
    width: 100%;
    outline: none;
    font-size: 18px;
    box-sizing: border-box;

    ${props =>
        props.error &&
        css`
            border: 1px solid red;
            color: red;
        `
    } 
`;

const SignupButton = styled.button`
    padding: 12px;
    margin: 10px 0;
    border-radius: 4px;
    border: 1px solid #dee2e6;
    background-color: #38d9a9;
    cursor: pointer;
    font-size: 18px;
    width: 100%;

    &:hover {
        background: #63e6be;
    }
`;

const SignupTemplate = () => {
    const dispatch = useAuthDispatch();

    const [email, setEmail] = useState('');
    const [name, setName] = useState('');
    const [firstPassword, setFirstPassword] = useState('');
    const [lastPassword, setLastPassword] = useState('');

    const [disable, setDisable] = useState(false);

    const [emailError, setEmailError] = useState('');
    const [nameError, setNameError] = useState('');
    const [firstPasswordError, setFirstPasswordError] = useState('');
    const [lastPasswordError, setLastPasswordError] = useState('');

    const onEmail = (event) => {
        const { target: { value } } = event;

        setEmail(value);

        if (value === '')
            setEmailError('ì´ë©”ì¼ì„ ì…ë ¥í•˜ì„¸ìš”.')
        else if (value.match(EMAIL_EXP) === null)
            setEmailError('ì´ë©”ì¼ í˜•ì‹ì— ë§ì§€ ì•ŠìŠµë‹ˆë‹¤.');
        else setEmailError('');
    };

    const onName = (event) => {
        const { target: { value } } = event;

        setName(value)

        if (value === '')
            setNameError('ì´ë¦„ì„ ì…ë ¥í•˜ì„¸ìš”.')
        else setNameError('');
    };

    const onFirstPassword = (event) => {
        const { target: { value } } = event;

        setFirstPassword(value);

        if (value === '')
            setFirstPasswordError('ë¹„ë°€ë²ˆí˜¸ë¥¼ ì…ë ¥í•˜ì„¸ìš”.')
        else if (value.match(PASSWORD_EXP) === null)
            setFirstPasswordError('ë¹„ë°€ë²ˆí˜¸ í˜•ì‹ì— ë§ì§€ ì•ŠìŠµë‹ˆë‹¤.')
        else setFirstPasswordError('');
    };

    const onLastPassword = (event) => {
        const { target: { value } } = event;

        setLastPassword(event.target.value)

        if (value === '')
            setLastPasswordError('ë¹„ë°€ë²ˆí˜¸ë¥¼ ì…ë ¥í•˜ì„¸ìš”.')
        else if (value !== firstPassword)
            setLastPasswordError('ë¹„ë°€ë²ˆí˜¸ê°€ ì¼ì¹˜í•˜ì§€ ì•ŠìŠµë‹ˆë‹¤.')
        else setLastPasswordError('');
    }

    const onSignup = async (event) => {
        event.preventDefault();

        const errors = [
            emailError,
            nameError,
            firstPasswordError,
            lastPasswordError
        ].join('');

        let error = "";

        if (email === '') {
            error = "ì´ë©”ì¼ì„ ì…ë ¥í•˜ì„¸ìš”."
            setEmailError(error)
        }

        if (name === '') {
            error = "ì´ë¦„ì„ ì…ë ¥í•˜ì„¸ìš”."
            setNameError(error)
        }

        if (firstPasswordError === '') {
            error = "ë¹„ë°€ë²ˆí˜¸ë¥¼ ì…ë ¥í•˜ì„¸ìš”."
            setFirstPasswordError(error)
        }

        if (lastPasswordError === '') {
            error = "ë¹„ë°€ë²ˆí˜¸ë¥¼ ì…ë ¥í•˜ì„¸ìš”."
            setLastPasswordError(error);
        }

        if (errors + error === '') {
            setDisable(true)
            const _auth = await findUserInGoogleSheet(email)

            if (_auth) {
                setEmailError('ì´ë¯¸ ì¡´ì¬í•˜ëŠ” ì´ë©”ì¼ ê³„ì •ì…ë‹ˆë‹¤.')
                setDisable(false)
            } else {
                const auth = await appendUserInGoogleSheet(email, name, firstPassword);
                dispatch({ type: "AUTH_SIGNUP", auth });
            }
        }
    }

    return (
        <>
            <SignupHeader />
            <SignupForm onSubmit={onSignup}>
                <SignupLabel error={emailError}>
                    {
                        emailError
                            ? emailError
                            : ("ì´ë©”ì¼")
                    }
                </SignupLabel>
                <SignupInput type="text" error={emailError} value={email} onChange={onEmail} disabled={disable} />

                <SignupLabel error={nameError}>
                    {
                        nameError
                            ? nameError
                            : ("ì´ë¦„")
                    }
                </SignupLabel>
                <SignupInput type="name" error={nameError} value={name} onChange={onName} disabled={disable} />

                <SignupLabel error={firstPasswordError}>
                    {
                        firstPasswordError
                            ? firstPasswordError
                            : ("ë¹„ë°€ë²ˆí˜¸")
                    }
                </SignupLabel>
                <SignupInput type="password" error={firstPasswordError} value={firstPassword} onChange={onFirstPassword} disabled={disable} />

                <SignupLabel error={lastPasswordError}>
                    {
                        lastPasswordError
                            ? lastPasswordError
                            : ("ë¹„ë°€ë²ˆí˜¸ ì¬ì…ë ¥")
                    }
                </SignupLabel>
                <SignupInput type="password" error={lastPasswordError} value={lastPassword} onChange={onLastPassword} disabled={disable} />
                <SignupButton type="submit" disabled={disable}>ì™„ë£Œ</SignupButton>
            </SignupForm>
        </>
    )
}

export default SignupTemplate